import React, { Component } from "react";
import PopUpContext from '../../context/popup-context';

export default class HomePage extends Component {
    render() {
        const { handlePopUpContent } = this.context;

        let userData = localStorage.getItem("user");
        userData = JSON.parse(userData);
        // console.log(userData);
        const userName = userData["response"][0]["name"].replace(/ .*/, '');
        return (
            <div className="home">
                <div className="illustration-area">
                    <div onClick={() => handlePopUpContent("addJoin")} className="magic-plus pc"></div>
                    <div onClick={() => handlePopUpContent("addJoin", { smallPopUp: true })} className="magic-plus mobile"></div>
                    <img className="illustration" src="/assets/illustration/new.svg" alt="Quick Start" />
                </div>
                <p>Hi, {userName}! Join or create a class <br />
                        by clicking on the "+"
                    </p>
            </div>
        );
    }
}

HomePage.contextType = PopUpContext;